import Cookies from 'universal-cookie';
const cookies = new Cookies();

 class Cookie {
    appName;

    constructor() {
        this.appName = `${process.env.REACT_APP_APP_NAME}`;
    }

    set(key, value) {

        key = `${this.appName}-${key}`;
        cookies.set(key, value, { path: '/' });

    };

    get(key) {
        key = `${this.appName}-${key}`;
        let getValue = cookies.get(key);
        if(getValue){
            return getValue;
        } else {
            return '';
        }
        
    };

    removeCookie(key) {
        key = `${this.appName}-${key}`;
        cookies.remove(key, { path: '/' });
        return true;

    }

}


export const cookieService=new Cookie();

