import axios from 'axios';
import { Redirect } from 'react-router-dom';
import { cookieService } from '../cookies/Cookies';
class HttpService {
    post(url, requestObj) {
        url = `${process.env.REACT_APP_API_URL}${url}`;
        return new Promise((resolve, reject) => {
            let request = {
                "device_unique_id": "dffnkjn43434",
                "operating_systems": "web"
            }

            let requestedData = { ...requestObj, ...request };


            let headers = {
                'Content-Type': 'application/json',
                'locale': 'en'
            }
            axios.post(url, requestedData, {
                headers: headers
            })
                .then((res) => {
                    // console.log("RESPONSE RECEIVED: ", res);
                    resolve(res);
                })
                .catch((err) => {
                    // console.log(" ERROR: ", err);
                    reject(err)

                })

        })

    }

    postForget(url, requestObj) {

        url = `${process.env.REACT_APP_API_URL}${url}`;
        return new Promise((resolve, reject) => {
            let request = {
                "device_unique_id": "dffnkjn43434",
                "operating_systems": "web"
            }

            let requestedData = { ...requestObj, ...request };


            let headers = {
                'Content-Type': 'application/json',
                'locale': 'en'
            }
            axios.post(url, requestedData, {
                headers: headers
            })
                .then((res) => {
                    // console.log("RESPONSE RECEIVED: ", res);
                    resolve(res)
                })
                .catch((err) => {
                    // console.log(" ERROR: ", err);
                    reject(err)

                })

        })

    }


    signOut = (url) => {
        url = `${process.env.REACT_APP_API_URL}${url}`;
        return new Promise((resolve, reject) => {
            const token = cookieService.get('authentication');

            let headers = {
                'Content-Type': 'application/json',
                'x-access-token': token,
                'locale': 'en'
            }
            let request = {
                "device_unique_id": "dffnkjn43434",
                "operating_systems": "web"
            }

            axios.post(url, request, {
                headers: headers
            })
                .then((res) => {

                    // console.log(res);
                    resolve(res);

                    cookieService.removeCookie('authentication');
                    cookieService.removeCookie('authorization');
                    <Redirect to='/login' />;

                })
                .catch((err) => reject(err))
        })
    }
    // accounts information form 
    accounts = (url, data) => {
        url = `${process.env.REACT_APP_API_URL}${url}`;
        return new Promise((resolve, reject) => {
            const token = cookieService.get('authentication');

            let headers = {
                'Content-Type': 'application/json',
                'x-access-token': token,
                'locale': 'en'
            }
            let request = {
                "device_unique_id": "dffnkjn43434",
                "operating_systems": "web"
            }

            let requestData = { ...data, ...request };
           
            axios.post(url, requestData, {
                headers: headers
            })
                .then((res) => {

                    resolve(res);
                    // console.log("accounts data", res);
                })
                .catch((err) => reject(err))
        })
    }


    accountsUpdate = (url, data) => {
        url = `${process.env.REACT_APP_API_URL}${url}`;
        return new Promise((resolve, reject) => {
            const token = cookieService.get('authentication');

            let headers = {
                'Content-Type': 'application/json',
                'x-access-token': token,
                'locale': 'en'
            }
            let request = {
                "device_unique_id": "dffnkjn43434",
                "operating_systems": "web"
            }

            let requestData = { ...data, ...request };
           
            axios.post(url, requestData, {
                headers: headers
            })
                .then((res) => {

                    resolve(res);
                    // console.log("accounts data", res);
                })
                .catch((err) => reject(err))
        })
    }

    accountsImage = (url, data) => {
        url = `${process.env.REACT_APP_API_URL}${url}`;
        return new Promise((resolve, reject) => {
            const token = cookieService.get('authentication');

            let headers = {
                'Content-Type': 'application/json',
                'x-access-token': token,
                'locale': 'en'
            }
         

           
           
            axios.post(url, data, {
                headers: headers
            })
                .then((res) => {

                    resolve(res);
                    // console.log("accounts data", res);
                })
                .catch((err) => reject(err))
        })
    }
}

export const httpService = new HttpService();
