
import React from 'react';
import { useState } from 'react';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import { cookieService } from '../../services/cookies/Cookies';
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import { Link } from 'react-router-dom';
import Typography from '@material-ui/core/Typography';

import Container from '@material-ui/core/Container';
import useStyles from './Logincss';
import Nav from '../Navbar/Nav';
import { httpService } from '../../services/httpservice/http.sevice';
import { useTranslation } from 'react-i18next';
import { useHistory } from "react-router-dom";
import { useEffect } from 'react';
import { login_Api } from '../../config/config';


const Login = () => {
    const { t, i18n } = useTranslation();
    const [username, setUserName] = useState('');
    const [password, setPassword] = useState('');

    const history = useHistory();

    useEffect(() => {  

    }, []);

    const signin = (e) => {
        httpService.post(login_Api, {
            "username": username,
            "password": password,
            "fcm_id": "ujedhdh"
        }).then(res => {
            console.log("login Response", res);
            if (res && res.data) {
                cookieService.set('authentication', res.data.data.token);
                cookieService.set('authorization', res.data.data.data);

            }
        }).catch(err => {
            console.log('err', err);
        })
        return;
    };

    const classes = useStyles();
    return (
        <div className="login-bg"  >
            <Nav></Nav>
            <div className="container">
                <div className="outcontainer1 pull-right ">
                    <Container component="main" maxWidth="xs">
                        <CssBaseline />
                        <div className={classes.paper} >
                            <form className={classes.form}  >
                                <Typography class={classes.username}>{t("LOGIN_PAGE.USERNAME")}</Typography>
                                <TextField
                                    className="userEmail"
                                    variant="outlined"
                                    margin="normal"
                                    required
                                    fullWidth
                                    id="emails"
                                    onChange={event => { setUserName(event.target.value) }}
                                    name="email"
                                    autoComplete="email"
                                    autoFocus
                                />
                                <Typography class={classes.password}>{t("LOGIN_PAGE.PASSWORD")}</Typography>
                                <Grid item xs >
                                    <Link className={classes.forgot} to="/forget" variant="body2">
                                        {t("LOGIN_PAGE.FORGOT")}
                                    </Link>
                                </Grid>
                                <TextField
                                    className="password"
                                    variant="outlined"
                                    margin="normal"
                                    required
                                    fullWidth
                                    name="password"
                                    onChange={event => { setPassword(event.target.value) }}
                                    type="password"
                                    id="password"
                                    autoComplete="current-password"
                                />
                                <FormControlLabel
                                    control={<Checkbox value="remember" color="primary" />}
                                    label={t("LOGIN_PAGE.REMEMBER")}
                                />
                                <Button
                                    onClick={signin}
                                    type="button"
                                    fullWidth
                                    variant="contained"
                                    color="primary"
                                    className={classes.submit}
                                >
                                    {t("LOGIN_PAGE.SIGNIN")}
                                </Button>
                                <Grid container>
                                    <Grid item xs>

                                    </Grid>
                                    <Grid item>
                                        <Link className={classes.signup} to="/register" variant="body2">
                                            {t("LOGIN_PAGE.SIGNUPFREE")}
                                        </Link>
                                    </Grid>
                                </Grid>
                            </form>
                        </div>
                        <Box mt={8}>

                        </Box>
                    </Container>
                </div>

            </div>
        </div>
    )

}



export default Login;